/**********************************
 * Country Distribution Component *
 **********************************/
import React from 'react';
import {connect} from 'react-redux';
import {withRouter} from 'react-router';

/** Styles. */
import {withStyles, withTheme} from '@material-ui/core/styles';
import {teal} from '@material-ui/core/colors';
import STYLE from './CountryDistribution.style';

/** Components. */
import {
  withWidth, Grid, Fade, Button, Typography, Tooltip
} from '@material-ui/core';
import {PageLoader} from '../../../share/loader';
import {VerticalGap} from '../../../share/gap';
import {Slide} from 'react-reveal';
import Flag from '../../../share/flag';

/** Util. */
import gradient from 'gradient-color';
import {redirect} from '../../../../utils/route';

/** Actions. */
import {searchBrandsByCategory} from '../../../../actions/brand';
import {updatePageLoad} from '../../../../dispatchers/loader';

/** Constants. */
import {COUNTRY_CODE_MAP} from '../../../../constants/code';
import ROUTING_PATHS from '../../../../constants/routes';
import {SEARCH_CATEGORY} from '../../../../constants/properties';
import {ANIMATION_TIMEOUT} from '../../../../constants/animate';

const TOP_N = 12;

class CountryDistribution extends React.Component {
  searchByCategory(name) {
    this.props.updatePageLoad(true);
    this.props.searchBrandsByCategory(SEARCH_CATEGORY.COUNTRY, name)
      .then(() => {
        redirect(this.props, ROUTING_PATHS.SEARCH);
        this.props.updatePageLoad(false);
      });
  }

  get Countries() {
    const {countries, classes} = this.props;
    if (!countries) {
      return <PageLoader />;
    }
    const backgroundColors = gradient(
      [teal[900], teal[500]], countries.length);
    return (
      <Grid container direction='row' spacing={1}>
        {countries.map(([country, count], idx) => (
          <Grid item xs={12} sm={6} key={idx}>
            <Slide right cascade delay={idx * 50}>
              <Button variant='contained' className={classes.button}
                style={{backgroundColor: backgroundColors.shift()}}
                onClick={() => this.searchByCategory(country)}>
                <Grid container direction='row' justify='space-between'>
                  <Flag size='lg' code={COUNTRY_CODE_MAP.get(country)} />
                  <Tooltip title={country} aria-label={country}>
                    <Typography variant='overline' noWrap
                      style={{maxWidth: '80%'}}>
                      {country}
                    </Typography>
                  </Tooltip>
                  <Typography variant='h6'>{count}</Typography>
                </Grid>
              </Button>
            </Slide>
          </Grid>
        ))}
      </Grid>
    );
  }

  render() {
    const {classes, width} = this.props;
    const isXs = width === 'xs';
    return (
      <Fade in={true} timeout={ANIMATION_TIMEOUT}>
        <Grid item className={classes.wrapper}>
          <Typography variant='h5' align={isXs ? 'center' : 'left'}>
            {TOP_N} Top sell countries
          </Typography>
          <VerticalGap size='md' />
          {this.Countries}
        </Grid>
      </Fade>
    );
  }
}


const mapStateToProps = state => {
  const {countryCounts} = state.countReducer;
  const countries = countryCounts
    .sort((a, b) => b[1] - a[1])
    .slice(0, TOP_N);
  return {countries};
}

const mapDispatchToProps = dispatch => {
  return {
    searchBrandsByCategory: (category, keyword) => dispatch(searchBrandsByCategory(category, keyword)),
    updatePageLoad: (isLoading) => dispatch(updatePageLoad(isLoading))
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(
  withWidth()(withStyles(STYLE)(withTheme(CountryDistribution)))
));
