/*******************
 * Form Components *
 *******************/
import React from 'react';

/** Components. */
import {
  TextField, InputLabel, FormControl, Chip, Select, MenuItem, Grid,
  FormHelperText, Typography
} from '@material-ui/core';
import ChipInput from 'material-ui-chip-input';
import {grey} from '@material-ui/core/colors';
import Theme from '../../constants/theme';

export const TextInput = ({
  hasError, name, value, label, helperText, placeholder,
  multiline, onChange, onBlur
}) => {
  return (
    <TextField
      placeholder={placeholder}
      InputLabelProps={{shrink: true}}
      required fullWidth margin="normal"
      multiline={multiline}
      error={hasError} helperText={helperText}
      name={name} label={label} value={value}
      onChange={onChange} onBlur={onBlur}
    />
  );
};

export const ImageBox = ({url, onClick, helperText, hasError}) => {
  return (
    <Grid container direction="column">
      <Grid container onClick={onClick}
        direction="row" justify="center" alignItems="center"
        style={{
          height: 200, padding: 24, borderRadius: 5,
          border: `2px ${url ? 'solid' : 'dashed'}
          ${hasError ? Theme.palette.secondary[500] : grey[200]}`,
          color: `${hasError ? Theme.palette.secondary[500] : grey[500]}`,
          cursor: 'pointer',
        }}>
        {url ?
          <img src={url} alt="logo" style={{height: '100%'}} /> :
          <Typography variant="body1">Upload the logo</Typography>
        }
      </Grid>
      <FormHelperText error={hasError}>{helperText}</FormHelperText>
    </Grid>
  );
};

export const MultipleSelect = ({
  hasError, label, name, value, options, helperText, onChange, onBlur
}) => {
  return (
    <FormControl required error={hasError} style={{width: '100%'}} >
      <InputLabel>{label}</InputLabel>
      <Select multiple value={value} name={name}
        onChange={onChange} onBlur={onBlur}
        renderValue={selected => (
          <div style={{display: 'flex', flexWrap: 'wrap'}}>
            {selected.map(selectedValue => (
              <Chip key={selectedValue} label={selectedValue} />
            ))}
          </div>
        )}>
        {options.map(option => (
          <MenuItem key={option} value={option}>{option}</MenuItem>
        ))}
      </Select>
      <FormHelperText>{helperText}</FormHelperText>
    </FormControl>
  );
};

function Placeholder(props) {
  const {selectProps, innerProps = {}, children} = props;
  return (
    <Typography color="textSecondary" {...innerProps}
      className={selectProps.classes.placeholder} >
      {children}
    </Typography>
  );
}

const components = {Placeholder}

export const SingleSelect = ({
  hasError, label, name, value, options, helperText, onChange, onBlur
}) => {
  return (
    <FormControl required error={hasError} style={{width: '100%'}}
      margin="normal">
      <InputLabel shrink>{label}</InputLabel>
      <Select value={value} name={name} components={components}
        placeholder={"hello single"}
        onChange={onChange} onBlur={onBlur}>
        {options.map(option => (
          <MenuItem key={option} value={option}>{option}</MenuItem>
        ))}
      </Select>
      <FormHelperText>{helperText}</FormHelperText>
    </FormControl>
  );
};

export const MultipleChipInput = ({
  label, name, value, hasError, helperText, placeholder,
  onAdd, onDelete, onBlur
}) => {
  return (
    <ChipInput
      required fullWidth margin="normal"
      placeholder={placeholder}
      InputLabelProps={{shrink: true}}
      error={hasError} helperText={helperText}
      label={label} name={name} value={value}
      onAdd={onAdd} onDelete={onDelete} onBlur={onBlur}
    />
  );
}
