/*****************
 * Nav Component *
 *****************/
import React from 'react';
import {withStyles} from '@material-ui/core/styles';
import {withRouter} from 'react-router';

/** Components. */
import {Grid, Tabs, Tab, Button} from '@material-ui/core';
import {DetailCard, DetailCardTitle} from '../share/detail';
import {VerticalGap} from '../share/gap';

/** Constant. */
import {DETAIL_PAGE} from '../../constants/properties';

/** Util. */
import {FRONT_END_BASE} from '../../constants/api';

export const NavTabs = withStyles(() => ({
  root: {margin: '0 auto 12px'},
  indicator: {backgroundColor: 'white'}
}))(Tabs);

export const NavTab = withStyles(() => ({
  root: {minWidth: '100px'}
}))(Tab);

class NavLinksCardComponent extends React.Component {
  render() {
    return (
      <DetailCard>
        <DetailCardTitle text="Navigation links" />
        <VerticalGap size="xs" />
        <Grid container direction="column">
          {DETAIL_PAGE.LINK_BUTTONS.map((button, idx) => (
            <Button key={idx} color="primary" variant="contained" size="small"
              href={`${FRONT_END_BASE}${button.link}`}
              style={{marginTop: 8, boxShadow: 'unset'}}>
              {button.displayName}
            </Button>
          ))}
        </Grid>
      </DetailCard>
    );
  }
}

export const NavLinksCard = withRouter(NavLinksCardComponent);
