/****************
 * Brand Action *
 ****************/

/** Utils. */
import * as countUtil from '../utils/count';

/** Dispatchers. */
import {
  setProductCount, setIndustryCount,
  setCountryCount, setCountOverview
} from '../dispatchers/count';

export const fetchCountOverview = () => {
  return dispatch => {
    return countUtil.getCountOverview().then(overviewCount => {
      dispatch(setCountOverview(overviewCount));
    });
  }
}

export const fetchProductCount = () => {
  return dispatch => {
    return countUtil.getCategoryCount('product').then(counts => {
      dispatch(setProductCount(counts));
    });
  }
}

export const fetchIndustryCount = () => {
  return dispatch => {
    return countUtil.getCategoryCount('industry').then(counts => {
      dispatch(setIndustryCount(counts));
    });
  }
};

export const fetchCountryCount = () => {
  return dispatch => {
    return countUtil.getCategoryCount('country').then(counts => {
      dispatch(setCountryCount(counts));
    });
  }
};
