/*******************
 * Cloudinary Util *
 *******************/

/** Image upload and handle. */
import upLoader from 'cloudinary/lib/v2/uploader';
import config from 'cloudinary/lib/config';

/** Constant. */
import {CLOUDINARY} from '../constants/secret';

config({
  api_key: CLOUDINARY.API_KEY,
  api_secret: CLOUDINARY.API_SECRET,
  cloud_name: CLOUDINARY.CLOUD_NAME
});

export const openImageWidget = (imagePublicId, updateImage) => {
  return window.cloudinary.createUploadWidget({
    cloud_name: CLOUDINARY.CLOUD_NAME,
    upload_preset: CLOUDINARY.UPLOAD_PRESET
  }, (_, result) => {
    if (result) {
      const {secure_url, public_id} = result[0];
      if (imagePublicId) {
        upLoader.destroy(imagePublicId);
      }
      updateImage(public_id, secure_url);
    }
  }).open();
}
