/****************
 * Brand Action *
 ****************/

/** Utils. */
import * as brandUtil from '../utils/brand';
import {parseBrandDetail} from '../utils/parse';

/** Dispatchers. */
import {
  setBrand, setBrands, setBrandLogos,
  setBrandPagination, setBrandSearch
} from '../dispatchers/brand';

export const addBrand = (brandData) => {
  return dispatch => {
    return brandUtil.postBrand(brandData).then(({id}) => {
      dispatch(setBrand(brandData));
      return id;
    });
  };
}

export const fetchBrand = (brandId) => {
  return dispatch => {
    return brandUtil.getBrand(brandId).then(brand => {
      brand = parseBrandDetail(brand);
      dispatch(setBrand(brand));
    });
  };
}

export const fetchBrands = (currentPage = 1) => {
  return dispatch => {
    return brandUtil.getBrands(currentPage).then(({brands, lastPage}) => {
      brands = brands.map(brand => parseBrandDetail(brand));
      dispatch(setBrands(brands));
      dispatch(setBrandPagination({currentPage, lastPage}));
    });
  };
}

export const searchBrandsByKeyword = searchKeyword => {
  return dispatch => {
    return brandUtil.getSearchBrandsByKeyword(searchKeyword).then(brands => {
      const searchBrands = brands.map(brand => parseBrandDetail(brand));
      dispatch(setBrandSearch({searchBrands, searchKeyword}));
    });
  };
}

export const searchBrandsByCategory = (category, searchKeyword) => {
  return dispatch => {
    return brandUtil.getSearchBrandsByCategory(category, searchKeyword).then(brands => {
      const searchBrands = brands.map(brand => parseBrandDetail(brand));
      dispatch(setBrandSearch({searchBrands, searchKeyword}));
    });
  };
}

export const fetchBrandLogos = () => {
  return dispatch => {
    return brandUtil.getBrandLogos().then(logos => {
      dispatch(setBrandLogos(logos));
    });
  };
}
