/**************
 * Brand Util *
 **************/
import {get, post} from './http';

import {
  BRAND_URL, BRANDS_URL, BRAND_LOGOS_URL, BRAND_SEARCH_URL
} from '../constants/api';
import {addQueryToUrl} from './route';

export const getBrand = brandId => get(`${BRAND_URL}/${brandId}`);
export const getBrandLogos = () => get(BRAND_LOGOS_URL);
export const postBrand = brandData => post(BRAND_URL, brandData);
export const getBrands = (page = 1, perPage = 6) => {
  const url = new URL(BRANDS_URL);
  const params = {page, perPage};
  for (const key in params) {
    url.searchParams.append(key, params[key]);
  }
  return get(url);
}
export const getSearchBrandsByKeyword = keyword =>
  get(addQueryToUrl(BRAND_SEARCH_URL, {keyword}));
export const getSearchBrandsByCategory = (category, keyword) =>
  get(addQueryToUrl(`${BRAND_SEARCH_URL}/${category}`, {keyword}));
