/*********************
 * Join Us Component *
 *********************/
import React from 'react';
import {withRouter} from 'react-router';
import {connect} from 'react-redux';
import {withTheme} from '@material-ui/core/styles';

/** Component. */
import {
  SectionHighlight, Section, SectionTitle, SectionSubTitle, Ribbon
} from '../../share/section';
import {VerticalGap, HorizontalGap} from '../../share/gap';
import {TextInput, MultipleChipInput, ImageBox} from '../../share/form';
import {Grid, Button, Fade, withWidth} from '@material-ui/core';
import SubmissionDialog from './SubmissionDialog';
import {PageLoader} from '../../share/loader';
import {AutoCompleteSelect} from '../../share/AutoComplete';

/** Action. */
import {addBrand} from '../../../actions/brand';
import {updateBrandSubmissionDialog} from '../../../dispatchers/dialog';

/** Utils. */
import {openImageWidget} from '../../../utils/cloudinary';
import {buildNewBrandForm, ARRAY_COLUMNS} from '../../../utils/form';
import {redirect} from '../../../utils/route';

/** Constants. */
import {JOIN_US_PAGE} from '../../../constants/properties';
import {MOCK_BRAND_FORM} from '../../../constants/mock';
import {ANIMATION_TIMEOUT} from '../../../constants/animate';

class JoinUs extends React.Component {
  state = {
    ...buildNewBrandForm(JOIN_US_PAGE.NEW_BRAND_FORM_COLUMNS),
    imagePublicId: '',
    imageUrl: '',
    isFormValid: false,
    sectionFocus: {
      brand: false,
      category: false,
      team: false
    }
  };

  updateForm = ({name, value}) => {
    const columnInfo = this.state[name];
    columnInfo.value = value;
    columnInfo.hasError = false;
    columnInfo.message = JOIN_US_PAGE.NEW_BRAND_FORM_COLUMNS[name].message;
    this.setState({[name]: columnInfo});
    this.validateForm();
  }

  addChipInput(chipInput, name) {
    const columnInfo = this.state[name];
    columnInfo.value.push(chipInput);
    columnInfo.hasError = false;
    columnInfo.message = JOIN_US_PAGE.NEW_BRAND_FORM_COLUMNS[name].message;
    this.setState({[name]: columnInfo});
  }

  removeChipInput(_, index, name) {
    const columnInfo = this.state[name];
    columnInfo.value.splice(index, 1);
    columnInfo.hasError = false;
    columnInfo.message = JOIN_US_PAGE.NEW_BRAND_FORM_COLUMNS[name].message;
    this.setState({[name]: columnInfo});
  }

  openImageUploadWidget(event) {
    event.preventDefault();
    openImageWidget(
      this.state.imagePublicId,
      (imagePublicId, imageUrl) => {
        this.setState({
          imagePublicId, imageUrl,
          logoUrl: {
            value: imageUrl,
            hasError: false,
            message: JOIN_US_PAGE.NEW_BRAND_FORM_COLUMNS.logoUrl.message
          }
        });
      }
    );
  }

  openSubmissionDialog(event) {
    event.preventDefault();
    this.props.updateBrandSubmissionDialog(true);
  }

  validateInput({name, value}) {
    if (!value || (ARRAY_COLUMNS.includes(name) && !value.length)) {
      this.setState({
        [name]: {
          value: value, hasError: true,
          message: 'This field cannot be empty'
        }
      });
    } else {
      this.setState({
        [name]: {
          value: value, hasError: false,
          message: JOIN_US_PAGE.NEW_BRAND_FORM_COLUMNS[name].message
        }
      });
    }
  }

  validateInputs() {
    for (const name in JOIN_US_PAGE.NEW_BRAND_FORM_COLUMNS) {
      const {value} = this.state[name];
      this.validateInput({name, value});
    }
    this.validateForm();
  }

  validateForm() {
    for (const name in JOIN_US_PAGE.NEW_BRAND_FORM_COLUMNS) {
      const {value} = this.state[name];
      if (!value || (ARRAY_COLUMNS.includes(name) && !value.length)) {
        this.setState({isFormValid: false});
        return;
      } else {
        const columnInfo = this.state[name];
        columnInfo.hasError = false;
        columnInfo.message = JOIN_US_PAGE.NEW_BRAND_FORM_COLUMNS[name].message;
        this.setState({[name]: columnInfo});
      }
    }
    this.setState({isFormValid: true});
  }

  sectionOnFocus(section) {
    const sectionFocus = {
      brand: false,
      category: false,
      team: false
    }
    sectionFocus[section] = true;
    this.setState({sectionFocus})
  }

  addMockData() {
    const form = this.state;
    for (const name in MOCK_BRAND_FORM) {
      form[name].value = MOCK_BRAND_FORM[name]
    }
    this.setState(form);
    this.validateForm();
  }

  submitBrandForm() {
    const brandData = {};
    for (const name in JOIN_US_PAGE.NEW_BRAND_FORM_COLUMNS) {
      brandData[name] = this.state[name].value;
    }
    this.props.addBrand(brandData).then(id => {
      redirect(this.props, `/brand/${id}`);
    });
  }

  get FormBody() {
    const {width, theme, isPageLoading} = this.props;
    const columnInfo = JOIN_US_PAGE.NEW_BRAND_FORM_COLUMNS;
    if (isPageLoading) {
      return <PageLoader />;
    }
    const isXs = width === 'xs';
    return (
      <Grid container
        style={{
          maxWidth: theme.spacing(70), margin: '0 auto',
          padding: isXs ? theme.spacing(1, 0) : theme.spacing(6, 0),
        }}>
        {/* ---Brand Section--- */}
        <Section onFocus={() => this.sectionOnFocus('brand')}>
          <SectionHighlight isFocus={this.state.sectionFocus.brand} />
          <Ribbon text='Section 1 of 3' />
          <SectionTitle text='Brand' />
          <SectionSubTitle text='Help us know your brand' />
          <VerticalGap size='md' />
          <ImageBox url={this.state.logoUrl.value}
            hasError={this.state.logoUrl.hasError}
            helperText={this.state.logoUrl.message}
            onClick={event => this.openImageUploadWidget(event)} />
          <TextInput
            name='name' label='Name'
            value={this.state.name.value}
            hasError={this.state.name.hasError}
            helperText={this.state.name.message}
            placeholder={columnInfo['name'].placeholder}
            onChange={(event) => this.updateForm(event.target)}
            onBlur={(event) => this.validateInput(event.target)}
          />
          <VerticalGap size='sm' />
          <TextInput
            name='website' label='Website'
            value={this.state.website.value}
            hasError={this.state.website.hasError}
            helperText={this.state.website.message}
            placeholder={columnInfo['website'].placeholder}
            onChange={(event) => this.updateForm(event.target)}
            onBlur={(event) => this.validateInput(event.target)}
          />
        </Section>


        {/* ---Category Section--- */}
        <VerticalGap size={isXs ? 'md' : 'lg'} />
        <Section onFocus={() => this.sectionOnFocus('category')}>
          <SectionHighlight isFocus={this.state.sectionFocus.category} />
          <Ribbon text='Section 2 of 3' />
          <SectionTitle text='Category' />
          <SectionSubTitle text='Let us marketing your brand' />
          <VerticalGap size='md' />
          <AutoCompleteSelect
            label='Industries' name='industries' isMulti={true}
            options={JOIN_US_PAGE.INDUSTRY_OPTIONS}
            hasError={this.state.industries.hasError}
            helperText={this.state.industries.message}
            placeholder={columnInfo['industries'].placeholder}
            value={this.state.industries.value}
            onChange={(selectedOptions = []) => {
              const value = selectedOptions ?
                selectedOptions.map(option => option.value) : [];
              return this.updateForm({name: 'industries', value})
            }}
            onBlur={() => this.validateInput({
              name: 'industries', value: this.state.industries.value
            })}
          />
          <VerticalGap size='md' />
          <VerticalGap size='md' />
          <AutoCompleteSelect
            label='Products' name='products' isMulti={true}
            options={JOIN_US_PAGE.PRODUCT_OPTIONS}
            hasError={this.state.products.hasError}
            helperText={this.state.products.message}
            placeholder={columnInfo['products'].placeholder}
            value={this.state.products.value}
            onChange={(selectedOptions = []) => {
              const value = selectedOptions ?
                selectedOptions.map(option => option.value) : [];
              return this.updateForm({name: 'products', value})
            }}
            onBlur={() => this.validateInput({
              name: 'products', value: this.state.products.value
            })}
          />
        </Section>


        {/* ---Team Section--- */}
        <VerticalGap size={isXs ? 'md' : 'lg'} />
        <Section onFocus={() => this.sectionOnFocus('team')}>
          <SectionHighlight isFocus={this.state.sectionFocus.team} />
          <Ribbon text='Section 3 of 3' />
          <SectionTitle text='Team' />
          <SectionSubTitle text='Tell us about your team' />
          <VerticalGap size='xs' />
          <AutoCompleteSelect
            label='Founded Year' name='foundedYear' isMulti={false}
            options={JOIN_US_PAGE.YEAR_OPTIONS}
            hasError={this.state.foundedYear.hasError}
            helperText={this.state.foundedYear.message}
            placeholder={columnInfo['foundedYear'].placeholder}
            value={this.state.foundedYear.value}
            onChange={(selectedOption) => {
              const value = selectedOption ? selectedOption.value : '';
              return this.updateForm({name: 'foundedYear', value});
            }}
            onBlur={() => this.validateInput({
              name: 'foundedYear',
              value: this.state.foundedYear.value
            })}
          />
          <VerticalGap size='md' />
          <VerticalGap size='md' />
          <AutoCompleteSelect
            label='Headquarter(s)' name='headquarterCountries' isMulti={true}
            options={JOIN_US_PAGE.COUNTRY_OPTIONS}
            hasError={this.state.headquarterCountries.hasError}
            helperText={this.state.headquarterCountries.message}
            placeholder={columnInfo['headquarterCountries'].placeholder}
            value={this.state.headquarterCountries.value}
            onChange={(selectedOptions = []) => {
              const value = selectedOptions ?
                selectedOptions.map(option => option.value) : [];
              return this.updateForm({name: 'headquarterCountries', value})
            }}
            onBlur={() => this.validateInput({
              name: 'headquarterCountries',
              value: this.state.headquarterCountries.value
            })}
          />
          <VerticalGap size='sm' />
          <MultipleChipInput
            label='Key People'
            name='keyPeople'
            value={this.state.keyPeople.value}
            hasError={this.state.keyPeople.hasError}
            helperText={this.state.keyPeople.message}
            placeholder={columnInfo['keyPeople'].placeholder}
            onAdd={(chipInput) => this.addChipInput(chipInput, 'keyPeople')}
            onDelete={(chipInput, index) => this.removeChipInput(chipInput, index, 'keyPeople')}
            onBlur={() => this.validateInput({
              name: 'keyPeople', value: this.state.keyPeople.value
            })}
          />
          <VerticalGap size='md' />
          <VerticalGap size='sm' />
          <TextInput
            multiline
            name='mission' label='Mission'
            value={this.state.mission.value}
            hasError={this.state.mission.hasError}
            helperText={this.state.mission.message}
            placeholder={columnInfo['mission'].placeholder}
            onChange={(event) => this.updateForm(event.target)}
            onBlur={(event) => this.validateInput(event.target)}
          />
          <TextInput
            multiline
            name='introduction' label='Introduction'
            value={this.state.introduction.value}
            hasError={this.state.introduction.hasError}
            helperText={this.state.introduction.message}
            placeholder={columnInfo['introduction'].placeholder}
            onChange={(event) => this.updateForm(event.target)}
            onBlur={(event) => this.validateInput(event.target)}
          />
        </Section>

        {/* ---Action Section--- */}
        <VerticalGap size={isXs ? 'md' : 'lg'} />
        <Grid container direction='row' justify='flex-end'>
          <Button variant='outlined' color='secondary'
            onClick={() => this.addMockData()}>
            Mock
          </Button>
          <HorizontalGap size='sm' />
          <Grid item onMouseOver={() => this.validateInputs()}>
            <Button variant='contained' color='primary'
              disabled={!this.state.isFormValid}
              onClick={event => this.openSubmissionDialog(event)}>
              Submit
            </Button>
          </Grid>
          {this.props.width === 'xs' && <HorizontalGap size='sm' />}
        </Grid>
        {/* ---Submission Dialog--- */}
        <SubmissionDialog submitBrandForm={() => this.submitBrandForm()} />
      </Grid>
    );
  }

  render() {
    return (
      <Fade in={true} timeout={ANIMATION_TIMEOUT}>
        {this.FormBody}
      </Fade>
    );
  }
}

const mapStateToProps = state => {
  const {isPageLoading} = state.loaderReducer;
  return {isPageLoading};
}

const mapDispatchToProps = dispatch => {
  return {
    addBrand: brandData => dispatch(addBrand(brandData)),
    updateBrandSubmissionDialog: (isOpen) =>
      dispatch(updateBrandSubmissionDialog(isOpen))
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(
  withWidth()(withTheme(JoinUs)))
);
