/**************
 * Route Util *
 **************/

export const addQueryToUrl = (url, params) => {
  url = new URL(url);
  for (const key in params) {
    url.searchParams.append(key, params[key]);
  }
  return url;
}

/** Redirects to props from props. */
export const redirect = (props, pathname, search = '') => {
  if (search) {
    search = `?keyword=${search}`;
  }
  const hasSamePathName = props.location.pathname === pathname;
  const hasSameSearch = props.location.search === search;
  if (!hasSamePathName || !hasSameSearch) {
    props.history.push({pathname, search});
  }
}
