/********************
 * Search Component *
 ********************/
import React from 'react';
import {connect} from 'react-redux';

/** Components. */
import {
  withWidth, Grid, Typography, Fade
} from '@material-ui/core';
import {VerticalGap} from '../../share/gap';
import {NavLinksCard} from '../../share/nav';
import {BrandCards} from '../../share/card';
import {PageLoader} from '../../share/loader';
import {DetailCard, DetailCardTitle} from '../../share/detail';
import CountUp from 'react-countup';

/** Constants. */
import {ANIMATION_TIMEOUT} from '../../../constants/animate';
import {PAGE_MAX_WIDTH} from '../../../constants/properties';

/** Actions. */
import {updatePageLoad} from '../../../dispatchers/loader';
class Search extends React.Component {
  get SearchContent() {
    const {searchBrands, isPageLoading} = this.props;
    if (isPageLoading) {
      return <PageLoader />;
    }
    if (!searchBrands.length) {
      return <Typography variant='h6'>Oops! No results found</Typography>;
    }
    return <BrandCards brands={searchBrands} fit='solo' />;
  }

  get SearchCount() {
    return (
      <DetailCard>
        <DetailCardTitle text='Count' />
        <VerticalGap size='xs' />
        <Grid container direction='column' alignItems='center'>
          <Typography variant='h3'>
            <CountUp start={0} end={this.props.searchBrands.length} />
          </Typography>
          <Typography variant='overline'>
            brands
          </Typography>
        </Grid>
      </DetailCard>
    );
  }

  render() {
    const {width} = this.props;
    const isXs = width === 'xs';
    return (
      <Fade in={true} timeout={ANIMATION_TIMEOUT}>
        <Grid>
          {!isXs && <VerticalGap size='lg' />}
          <Grid container spacing={2}
            direction={isXs ? 'column-reverse' : 'row'}
            justify={isXs ? 'flex-start' : 'space-between'}
            style={{
              maxWidth: PAGE_MAX_WIDTH, width: 'auto',
              margin: '0 auto'
            }}>
            <Grid item xs={12} sm={8}>
              {this.SearchContent}
            </Grid>
            <Grid item xs={12} sm={4}>
              {<NavLinksCard />}
              <VerticalGap size='md' />
              {this.SearchCount}
            </Grid>
          </Grid>
        </Grid>
      </Fade>
    )
  }
}

const mapStateToProps = state => {
  const {isPageLoading} = state.loaderReducer;
  const {searchKeyword, searchBrands} = state.brandReducer;
  return {isPageLoading, searchKeyword, searchBrands: searchBrands.slice()};
}

const mapDispatchToProps = dispatch => {
  return {
    updatePageLoad: (isLoading) => dispatch(updatePageLoad(isLoading))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withWidth()(Search));
