/*******************
 * Card Components *
 *******************/
import React from 'react';
import {connect} from 'react-redux';
import {withRouter} from 'react-router';

/** Styles. */
import {withStyles, withTheme} from '@material-ui/core/styles';
import STYLE from './card.style';
import './card.scss';

/** Components. */
import {withWidth, Link, Grid, Typography, Badge} from '@material-ui/core';
import {HorizontalGap} from './gap';
import {Zoom} from 'react-reveal';

/** Util. */
import {redirect} from '../../utils/route';

/** Action. */
import {updatePageLoad} from '../../dispatchers/loader';
import {searchBrandsByCategory} from '../../actions/brand';

/** Constants. */
import {LEFT_ZOOM_TIMEOUT, RIGHT_ZOOM_TIMEOUT} from '../../constants/animate';
import {SEARCH_CATEGORY} from '../../constants/properties';
import {FRONT_END_BASE} from '../../constants/api';
import ROUTING_PATHS from '../../constants/routes';
import {blue} from '@material-ui/core/colors';

class BrandCardComponent extends React.Component {
  searchByCategory(category, name) {
    redirect(this.props, ROUTING_PATHS.SEARCH);
    this.props.updatePageLoad(true);
    this.props.searchBrandsByCategory(category, name)
      .then(() => this.props.updatePageLoad(false));
  }

  render() {
    const {classes, theme, width, brand, fit, isOdd} = this.props;
    const isXs = width === 'xs';
    const {
      id, name, logoUrl, industries, mission, website, articleCount
    } = brand;
    const animateOptions = {
      cascade: true,
      ...(!isOdd && {left: true, duration: LEFT_ZOOM_TIMEOUT}),
      ...(isOdd && {right: true, duration: RIGHT_ZOOM_TIMEOUT}),
    }
    return (
      <Grid item xs={12} sm={fit === 'merge' ? 6 : 12}
        style={{
          display: 'flex', marginBottom: isXs ? 48 : 16,
          padding: theme.spacing(2)
        }}>
        <Zoom {...animateOptions}>
          <Grid container direction={isXs ? 'column' : 'row'} justify='center'
            style={{height: '100%'}}>
            <Grid item sm={3} className={classes.logoWrapper}>
              <Link href={`${FRONT_END_BASE}/brand/${id}`}
                style={{textDecoration: 'none'}}>
                <Badge badgeContent={articleCount} color='secondary'>
                  {logoUrl ?
                    <img className={classes.imageLogo} src={logoUrl}
                      alt='brand-logo' /> :
                    <Grid container className={classes.textLogo}
                      direction='row' justify='center' alignItems='center'>
                      <Typography>{id}</Typography>
                    </Grid>}
                </Badge>
              </Link>
            </Grid>
            <Grid item sm={9}>
              <Grid container direction='column' style={{height: '100%'}}>
                <Typography variant='h6'>
                  {name}
                </Typography>
                <Grid item>
                  <Grid container>
                    {industries && industries.map((industry, idx) => (
                      <Typography variant='overline' key={idx}
                        className={classes.industryLink}
                        style={{color: blue[800]}}
                        onClick={() => this.searchByCategory(
                          SEARCH_CATEGORY.INDUSTRY, industry)}>
                        {industry}
                      </Typography>
                    ))}
                  </Grid>
                </Grid>
                <Typography variant='caption' gutterBottom>
                  {mission}
                </Typography>

                {/* ---Links--- */}
                <Grid container direction='row' alignItems='center'
                  style={{marginTop: 'auto'}}>
                  <Link component='a' variant='caption' target='_blank'
                    href={website}>
                    Website
                    </Link >
                  <HorizontalGap size='sm' />
                  <Link component='a' variant='caption'
                    style={{cursor: 'pointer'}}
                    href={`${FRONT_END_BASE}/brand/${id}`}>
                    Details
                </Link >
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Zoom>
      </Grid>
    );
  }
}

const parentProps = () => {
  return {};
}

const eventFunctions = dispatch => {
  return {
    searchBrandsByCategory: (category, keyword) =>
      dispatch(searchBrandsByCategory(category, keyword)),
    updatePageLoad: (isLoading) => dispatch(updatePageLoad(isLoading)),
  }
}

export const BrandCard = withRouter(
  connect(parentProps, eventFunctions)(withWidth()(
    withStyles(STYLE)(withTheme(BrandCardComponent))))
);

export const BrandCards = ({brands, fit}) => {
  return brands.map(
    (brand, idx) =>
      <BrandCard key={idx} brand={brand} fit={fit} isOdd={idx & 1} />
  );
};
