import MomentTimezone from 'moment-timezone';
import {TIMEZONE_MAP} from '../constants/time';

export const getLocalTimezone = () => {
  const timezone = Intl.DateTimeFormat().resolvedOptions().timeZone
  const timezoneAbbreviation = MomentTimezone.tz(timezone).format('z');
  return TIMEZONE_MAP[timezoneAbbreviation];
}
