/**********************
 * Project Data Store *
 **********************/
import thunk from 'redux-thunk';
import {combineReducers, createStore, applyMiddleware} from 'redux';

/** Reducers. */
import brandReducer from './data/brand';
import countReducer from './data/count';
import loaderReducer from './ui/loader';
import dialogReducer from './ui/dialog';

export default createStore(
  combineReducers({
    brandReducer,
    countReducer,
    loaderReducer,
    dialogReducer
  }),
  applyMiddleware(thunk)
);
