/*************
 * Form Util *
 *************/

export const STRING_COLUMNS = [
  'logUrl', 'name', 'foundedYear', 'mission', 'introduction', 'website'
];

export const ARRAY_COLUMNS = [
  'industries', 'products', 'headquarterCountries', 'keyPeople'
];

export const buildNewBrandForm = (columns) => {
  let form = {};
  for (const columnName in columns) {
    const {message} = columns[columnName]
    const value =
      STRING_COLUMNS.includes(columnName) ? '' :
        ARRAY_COLUMNS.includes(columnName) ? [] : '';
    form[columnName] = {
      value,
      message,
      hasError: false,
    }
  }
  return form;
}
