/**************************
 * Brand Detail Component *
 **************************/
import React from 'react';
import {withRouter} from 'react-router';
import {connect} from 'react-redux';
import {withTheme, withStyles} from '@material-ui/core/styles';

/** Action. */
import {fetchBrand} from '../../actions/brand';
import {updatePageLoad} from '../../dispatchers/loader';
import {fetchBrandArticles} from '../../actions/article';
import {searchBrandsByCategory} from '../../actions/brand';
import {setBrand} from '../../dispatchers/brand';

/** Components. */
import {
  withWidth, Grid, AppBar, Typography, Fade, Tooltip
} from '@material-ui/core';
import {VerticalGap, HorizontalGap} from '../share/gap';
import {
  DetailCard, DetailCardTitle, DetailLogo
} from '../share/detail';
import Clock from 'react-clock';
import Moment from 'moment';
import Flag from '../share/flag';
import {PageLoader} from '../share/loader';
import {NavLinksCard} from '../share/nav';

/** Constants. */
import {ANIMATION_TIMEOUT} from '../../constants/animate';
import {MINUTE_IN_MILLISECONDS} from '../../constants/time';
import {COUNTRY_CODE_MAP} from '../../constants/code';
import STYLE from './BrandDetail.style';
import {SEARCH_CATEGORY} from '../../constants/properties';
import ROUTING_PATHS from '../../constants/routes';

/** Util. */
import {getLocalTimezone} from '../../utils/time';
import {redirect} from '../../utils/route';

class BrandDetail extends React.Component {
  state = {
    time: new Date(),
    brandId: -1,
    articles: []
  }

  componentWillUnmount() {
    window.clearInterval(this.timer);
    this.props.setBrand({});
  }

  componentDidMount() {
    this.timer = window.setInterval(() => this.setState({
      time: new Date()
    }), MINUTE_IN_MILLISECONDS);
  }

  static getDerivedStateFromProps(props, state) {
    const {id} = props.match.params;
    if (state.brandId !== id) {
      state.brandId = id;
      props.updatePageLoad(true);
      props.fetchBrand(id).then(() => {
        props.fetchBrandArticles(id).then(articles => {
          state.articles = articles;
          props.updatePageLoad(false);
        })
      });
    }
    return null;
  }

  searchByCategory(category, name) {
    redirect(this.props, ROUTING_PATHS.SEARCH);
    this.props.updatePageLoad(true);
    this.props.searchBrandsByCategory(category, name)
      .then(() => {
        this.props.updatePageLoad(false);
      });
  }

  get NameCard() {
    const {classes, brand} = this.props;
    const {logoUrl, name, industries, id} = brand;
    return (
      <AppBar position='static' elevation={0}>
        <Grid container direction='row' alignItems='center'
          className={classes.nameCard}>
          <Grid item xs={12} ms={4}>
            <Grid container justify='center' alignItems='center'
              className={classes.logo}>
              {logoUrl ?
                <DetailLogo src={logoUrl} alt='brand-logo' /> :
                <Typography variant='h5'>{id}</Typography>}
            </Grid>
          </Grid>
          <Grid item ms={6}>
            <Grid container direction='column' justify='center'>
              <Typography variant='h5'>
                {name}
              </Typography>
              <Grid item>
                <Grid container direction='row'>
                  {industries && industries.map((industry, idx) => (
                    <Typography variant='overline' className={classes.industryLink}
                      key={idx} onClick={() => this.searchByCategory(
                        SEARCH_CATEGORY.INDUSTRY, industry)}>
                      {industry}
                    </Typography>
                  ))}
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </AppBar>
    );
  }

  get Mission() {
    const {mission} = this.props.brand;
    return (
      <DetailCard>
        <DetailCardTitle text='Mission' />
        <VerticalGap size='sm' />
        <Typography variant='body1'>{mission}</Typography>
      </DetailCard>
    );
  }

  get Products() {
    const {classes} = this.props;
    const {products} = this.props.brand;
    return (
      <DetailCard>
        <DetailCardTitle text='Products' />
        <VerticalGap size='sm' />
        {products && products.map((product, idx) => (
          <div key={idx} className={classes.chip}
            onClick={() =>
              this.searchByCategory(SEARCH_CATEGORY.PRODUCT, product)}>
            <Typography variant='caption'>{product}</Typography>
          </div>
        ))}
      </DetailCard>
    );
  }

  get Introduction() {
    const {introduction} = this.props.brand;
    return (
      <DetailCard>
        <DetailCardTitle text='Introduction' />
        <VerticalGap size='sm' />
        <Typography variant='body1'>{introduction}</Typography>
      </DetailCard>
    );
  }

  get LocalTime() {
    const {time} = this.state;
    return (
      <DetailCard>
        <DetailCardTitle text='Local time' />
        <VerticalGap size='sm' />
        <Grid container direction='row' alignItems='center'>
          <Clock value={time} size={60} renderSecondHand={false}
            renderMinuteMarks={false} renderHourMarks={false}
            minuteHandWidth={2} hourHandWidth={2} />
          <HorizontalGap size='sm' />
          <Grid item>
            <Grid container direction='column' justify='center'>
              <Typography variant='h6'>{Moment(time).format('LT')}</Typography>
              <Typography variant='caption'>{getLocalTimezone()}</Typography>
            </Grid>
          </Grid>
        </Grid>
      </DetailCard>
    );
  }

  get KeyPeople() {
    const {keyPeople} = this.props.brand;
    return (
      <DetailCard>
        <DetailCardTitle text='Key people' />
        <VerticalGap size='sm' />
        <Grid container direction='column'>
          {keyPeople && keyPeople.map((person, idx) => (
            <Typography variant='body2' key={idx}>{person}</Typography>
          ))}
        </Grid>
      </DetailCard>
    );
  }

  get HeadquarterCountries() {
    const {brand, theme} = this.props;
    const {headquarterCountries} = brand;
    return (
      <DetailCard>
        <DetailCardTitle text='Headquarter(s)' />
        <Grid container direction='column'>
          {headquarterCountries &&
            headquarterCountries.map((country, idx) => {
              return (
                <Grid item key={idx} style={{width: '100%'}}>
                  <VerticalGap size='sm' />
                  <Grid container direction='row' alignItems='center'
                    style={{flexWrap: 'inherit'}}>
                    <Grid item>
                      <Flag size='lg' code={COUNTRY_CODE_MAP.get(country)} />
                    </Grid>
                    <Grid item sm={9} style={{marginLeft: theme.spacing(1)}}>
                      <Tooltip title={country} aria-label={country}>
                        <Typography variant='body2' noWrap>
                          {country}
                        </Typography>
                      </Tooltip>
                    </Grid>
                  </Grid>
                </Grid>
              )
            })}
        </Grid>
      </DetailCard>
    );
  }

  get FoundedTime() {
    const {foundedYear} = this.props.brand;
    return (
      <DetailCard>
        <DetailCardTitle text='Founded Time' />
        <VerticalGap size='sm' />
        <Grid container direction='column'>
          <Typography variant='h6'>{foundedYear}</Typography>
          <Typography variant='caption'>
            {`- ${Moment(foundedYear, 'YYYY').fromNow()}`}
          </Typography>
        </Grid>
      </DetailCard>
    );
  }

  get DetailBody() {
    const {width, classes, isPageLoading} = this.props;
    if (isPageLoading) {
      return <PageLoader />;
    }
    const isXs = width === 'xs';
    return (
      <Fade in={true} timeout={ANIMATION_TIMEOUT}>
        <Grid container direction='column' className={classes.bodyWrapper}>
          {/* Brand Detail */}
          <Grid item style={{width: '100%'}}>
            <Grid container direction={isXs ? 'column-reverse' : 'row'}
              justify='space-between'>
              {/* Body LEFT */}
              <Grid item sm={7}>
                <Grid container direction='column'>
                  {this.Mission}
                  <VerticalGap size='lg' />
                  {this.Products}
                  <VerticalGap size='lg' />
                  {this.Introduction}
                </Grid>
              </Grid>

              {/* Body RIGHT */}
              <Grid item sm={4} style={{width: '100%'}}>
                {this.LocalTime}
                <VerticalGap size='lg' />
                {<NavLinksCard />}
                <VerticalGap size='lg' />
                {this.KeyPeople}
                <VerticalGap size='lg' />
                {this.HeadquarterCountries}
                <VerticalGap size='lg' />
                {this.FoundedTime}
                {isXs && <VerticalGap size='lg' />}
              </Grid>
            </Grid>
          </Grid>

          {/* Brand Article */}
          <Grid item>
            {this.BrandArticles}
          </Grid>
        </Grid>
      </Fade>
    );
  }

  get BrandArticles() {
    return (
      <Grid container direction='column'>
        {this.state.articles.map(({title, postedAt, summary}, idx) => (
          <Grid key={idx}>
            <VerticalGap size='lg' />
            <DetailCard>
              <Typography variant='h6'>{title}</Typography>
              <VerticalGap size='sm' />
              <Typography variant='caption'>
                {`- ${postedAt}`}
              </Typography>
              <VerticalGap size='sm' />
              <Typography variant='body2' key={idx}>
                {summary}
              </Typography>
            </DetailCard>
          </Grid>
        ))}
      </Grid>
    );
  }

  render() {
    return (
      <Grid container direction='column'>
        {this.NameCard}
        {this.DetailBody}
      </Grid>
    );
  }
}

const mapStateToProps = state => {
  const {brand} = state.brandReducer;
  const {isPageLoading} = state.loaderReducer;
  return {brand, isPageLoading};
}

const mapDispatchToProps = dispatch => {
  return {
    searchBrandsByCategory: (category, keyword) =>
      dispatch(searchBrandsByCategory(category, keyword)),
    fetchBrand: brandId => dispatch(fetchBrand(brandId)),
    updatePageLoad: (isLoading) => dispatch(updatePageLoad(isLoading)),
    fetchBrandArticles: brandId => dispatch(fetchBrandArticles(brandId)),
    setBrand: brand => dispatch(setBrand(brand))
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(
  withWidth()(withStyles(STYLE)(withTheme(BrandDetail)))
));

