/*****************
 * Brand Reducer *
 *****************/

/** Constant. */
import {Brand} from '../../constants/redux';

const defaultState = Object.freeze({
  brand: {},
  brandMap: new Map(), // <Brand ID: number><Brand Object: Brand>
  brands: [],
  logos: [],
  currentPage: 1,
  lastPage: 0,
  searchKeyword: '',
  searchBrands: [],
});

export default (state = defaultState, action) => {
  const {brandMap} = state;
  switch (action.type) {
    case Brand.UPDATE_BRAND:
      const brand = action.data;
      brandMap.set(brand.id, brand)
      return Object.assign({}, state, {brand});
    case Brand.UPDATE_BRANDS:
      return Object.assign({}, state, {brands: action.data});
    case Brand.UPDATE_BRAND_LOGOS:
      const logos = action.data.slice();
      return Object.assign({}, state, {logos});
    case Brand.UPDATE_BRAND_PAGINATION:
      const {currentPage, lastPage} = action.data;
      return Object.assign({}, state, {currentPage, lastPage});
    case Brand.UPDATE_BRAND_SEARCH:
      const {searchBrands, searchKeyword} = action.data;
      return Object.assign({}, state, {searchBrands, searchKeyword});
    default:
      return state;
  }
}
