/*******************************
 * Submission Dialog Component *
 *******************************/
import React from 'react';
import {withRouter} from 'react-router';
import {connect} from 'react-redux';

/** Components. */
import {
  DialogTitle, DialogActions, Dialog, DialogContent, DialogContentText,
  Button, Grid
} from '@material-ui/core';
import {HorizontalGap} from '../../share/gap';

/** Action. */
import {updateBrandSubmissionDialog} from '../../../dispatchers/dialog';

class SubmissionDialog extends React.Component {
  closeDialog(event) {
    event.preventDefault();
    this.props.updateBrandSubmissionDialog(false);
  }

  confirmFormSubmit(event) {
    event.preventDefault();
    this.props.submitBrandForm();
    this.props.updateBrandSubmissionDialog(false);
  }

  render() {
    return (
      <Dialog maxWidth='xs'
        open={this.props.isBrandSubmissionDialogOpen}>
        <DialogTitle>Please confirm the form mission.</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Once the form information is submitted. You cannot edit the content.
            </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Grid container direction='row' justify='flex-end'>
            <Button variant='outlined' color='secondary'
              onClick={event => this.closeDialog(event)}>
              Back to review
            </Button>
            <HorizontalGap size='sm' />
            <Button variant='contained' color='primary'
              onClick={event => this.confirmFormSubmit(event)}>
              Confirm
            </Button>
          </Grid>
        </DialogActions>
      </Dialog>
    )
  }
}

const mapStateToProps = state => {
  const {isBrandSubmissionDialogOpen} = state.dialogReducer;
  return {isBrandSubmissionDialogOpen};
}

const mapDispatchToProps = dispatch => {
  return {
    updateBrandSubmissionDialog: (isOpen) =>
      dispatch(updateBrandSubmissionDialog(isOpen))
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(
  SubmissionDialog
));
