/**********************
 * Overview Component *
 **********************/
import React from 'react';
import {connect} from 'react-redux';
import {withStyles, withTheme} from '@material-ui/core/styles';
import {orange, blueGrey, lightBlue, teal} from '@material-ui/core/colors';

/** Components. */
import {withWidth, Grid, Typography, Fade, Paper} from '@material-ui/core';
import {Flip} from 'react-reveal';

/** Constants. */
import {ANIMATION_TIMEOUT} from '../../../../constants/animate';
import {INSIGHT_PAGE} from '../../../../constants/properties';
import STYLE from './Overview.style';

const CARD_BACKGROUNDS = [
  orange[800],
  blueGrey[800],
  lightBlue[800],
  teal[800]
];


class Overview extends React.Component {
  get StatisticCounts() {
    const {count, setDisplay, classes, theme} = this.props;
    return (
      <Grid container direction='row' justify='space-between' spacing={3}>
        {INSIGHT_PAGE.OVERVIEW_COUNTS.map(({code, text, name}, idx) => (
          <Grid item xs={12} sm={6} key={idx}>
            <Paper className={classes.card} onClick={() => setDisplay(code)}
              style={{background: CARD_BACKGROUNDS[idx]}}>
              <Flip top cascade delay={idx * 50}>
                <Grid container direction='column' alignItems='center'
                  style={{color: theme.palette.common.white}}>
                  <Typography variant='h1'>
                    {count[name]}
                  </Typography>
                  <Typography variant='overline'>{text}</Typography>
                </Grid>
              </Flip>
            </Paper>
          </Grid>
        ))}
      </Grid>
    );
  }

  render() {
    return (
      <Fade in={true} timeout={ANIMATION_TIMEOUT}>
        <Grid item xs={12} sm={12} md={12}
          style={{padding: this.props.theme.spacing(3)}}>
          {this.StatisticCounts}
        </Grid>
      </Fade>
    );
  }
}

const mapStateToProps = state => {
  const {overviewCount} = state.countReducer;
  const count = Object.freeze(overviewCount);
  return {count};
};

const mapDispatchToProps = _ => {
  return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(
  withStyles(STYLE)(withWidth()(withTheme(Overview)))
);
