/******************
 * Home Component *
 ******************/
import React from 'react';
import {connect} from 'react-redux';

/** Components. */
import {AppBar, Grid} from '@material-ui/core';
import {NavTabs, NavTab} from '../share/nav';
import Gallery from './gallery/Gallery';
import JoinUs from './join-us/JoinUs';
import Insight from './insight/Insight';
import Search from './search/Search';
import {PageLoader} from '../share/loader';

/** Constants. */
import ROUTING_PATHS from '../../constants/routes';
import {FRONT_END_BASE} from '../../constants/api';
import {HOME_PAGE} from '../../constants/properties';

/** Utils. */
import {redirect} from '../../utils/route';

/** Action. */
import {fetchBrands, fetchBrandLogos} from '../../actions/brand';
import {
  fetchProductCount, fetchIndustryCount, fetchCountryCount,
  fetchCountOverview
} from '../../actions/count';
import {updatePageLoad} from '../../dispatchers/loader';

/** Home Component. */
class Home extends React.Component {
  state = {
    path: ROUTING_PATHS.JOIN_US,
  }

  componentDidMount() {
    this.props.updatePageLoad(true);
    this.props.fetchProductCount();
    this.props.fetchIndustryCount();
    this.props.fetchCountryCount();
    this.props.fetchCountOverview();
    this.props.fetchBrandLogos();
    this.props.fetchBrands(this.props.onPage).then(() => {
      this.props.updatePageLoad(false);
    });
  }

  static getDerivedStateFromProps(props, state) {
    state.path = props.location.pathname;
    return null;
  }

  updateOnTabClick(path) {
    this.setState({path});
    redirect(this.props, path);
  }

  get SwitchBar() {
    return (
      <AppBar position='static' elevation={0}>
        <NavTabs value={this.state.path}>
          {HOME_PAGE.NAV_TABS.map((navTab, idx) => (
            <NavTab key={idx} label={navTab.label} value={navTab.path}
              href={`${FRONT_END_BASE}${navTab.path}`}
              onClick={() => this.updateOnTabClick(navTab.path)} />
          ))}
        </NavTabs>
      </AppBar>
    );
  }

  get SwitchContent() {
    if (this.props.isPageLoading) {
      return <PageLoader />;
    }
    switch (this.state.path) {
      case ROUTING_PATHS.GALLERY:
        return <Gallery />;
      case ROUTING_PATHS.JOIN_US:
        return <JoinUs />;
      case ROUTING_PATHS.INSIGHT:
        return <Insight />;
      default:
        return null;
    }
  }

  get SwitchBody() {
    return (
      <Grid container direction='column'>
        {this.SwitchBar}
        {this.SwitchContent}
      </Grid>
    );
  }

  get SearchBody() {
    return (
      <Search />
    );
  }



  render() {
    return this.state.path === ROUTING_PATHS.SEARCH ?
      this.SearchBody :
      this.SwitchBody;
  }
}

const mapStateToProps = state => {
  const {isPageLoading} = state.loaderReducer;
  const {page} = state.brandReducer;
  const onPage = page;
  return {isPageLoading, onPage};
}

const mapDispatchToProps = dispatch => {
  return {
    fetchBrands: onPage => dispatch(fetchBrands(onPage)),
    fetchProductCount: () => dispatch(fetchProductCount()),
    fetchIndustryCount: () => dispatch(fetchIndustryCount()),
    fetchCountryCount: () => dispatch(fetchCountryCount()),
    fetchCountOverview: () => dispatch(fetchCountOverview()),
    fetchBrandLogos: () => dispatch(fetchBrandLogos()),
    updatePageLoad: (isLoading) => dispatch(updatePageLoad(isLoading))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Home);
