/******************
 * Loader Reducer *
 ******************/

/** Constant. */
import {Loader} from '../../constants/redux';

const defaultState = Object.freeze({
  isPageLoading: true,
})

export default (state = defaultState, action) => {
  switch (action.type) {
    case Loader.LOAD_PAGE:
      return Object.assign({}, state, {isPageLoading: true});
    case Loader.LOADED_PAGE:
      return Object.assign({}, state, {isPageLoading: false});
    default:
      return state;
  }
}
