/** Constant. */
import {Count} from '../constants/redux';

export const setProductCount = (counts) => ({
  type: Count.UPDATE_PRODUCT_COUNT, data: counts
});

export const setIndustryCount = (counts) => ({
  type: Count.UPDATE_INDUSTRY_COUNT, data: counts
});

export const setCountryCount = (counts) => ({
  type: Count.UPDATE_COUNTRY_COUNT, data: counts
});

export const setCountOverview = overviewCount => ({
  type: Count.UPDATE_COUNT_OVERVIEW, data: overviewCount
});
