import React from 'react';

/** Import route module. */
import {Switch, Route, Redirect} from 'react-router-dom';

/** Import components. */
import Home from '../home/Home';
import BrandDetail from '../brand-detail/BrandDetail';

/** Import types. */
import ROUTING_PATHS from '../../constants/routes';

/** Integrated routing component for controlling the project route switch. */
export default () => (
  <Switch>
    <Route path={ROUTING_PATHS.GALLERY} component={Home} exact />
    <Route path={ROUTING_PATHS.JOIN_US} component={Home} exact />
    <Route path={ROUTING_PATHS.INSIGHT} component={Home} exact />
    <Route path={ROUTING_PATHS.SEARCH} component={Home} exact />
    <Route path={ROUTING_PATHS.BRAND_DETAIL} component={BrandDetail} exact />
    <Redirect from={ROUTING_PATHS.ROOT} to={ROUTING_PATHS.GALLERY} exact />
  </Switch>
);
