/********************
 * Brand Dispatcher *
 ********************/

/** Constant. */
import {Brand} from '../constants/redux';

export const setBrand = (brandData) => ({
  type: Brand.UPDATE_BRAND, data: brandData
});

export const setBrands = (brands) => ({
  type: Brand.UPDATE_BRANDS, data: brands
});

export const setBrandLogos = logos => ({
  type: Brand.UPDATE_BRAND_LOGOS, data: logos
});

export const setBrandPagination = info => ({
  type: Brand.UPDATE_BRAND_PAGINATION, data: info
});

export const setBrandSearch = info => ({
  type: Brand.UPDATE_BRAND_SEARCH, data: info
})
