/*********************
 * Brand Wall Component *
 *********************/
import React from 'react';
import {withRouter} from 'react-router';
import {connect} from 'react-redux';
import {withStyles, withTheme} from '@material-ui/core/styles';

/** Components. */
import {Grid, Fade, Typography, Card} from '@material-ui/core';
import {Flip} from 'react-reveal';

/** Util. */
import {redirect} from '../../../../utils/route';

/** Constant. */
import {ANIMATION_TIMEOUT} from '../../../../constants/animate';
import STYLE from './BrandWall.style';

class BrandWall extends React.Component {
  get Logos() {
    const {logoUrls, classes} = this.props;
    return (
      <Grid container spacing={3}>
        {logoUrls.map(({id, url}) => (
          <Grid key={id} item xs={6} sm={3}>
            <Card className={classes.wrapper}
              onClick={() => redirect(this.props, `/brand/${id}`)}>
              <Flip right cascade>
                <Grid container direction='column'
                  justify='center' alignItems='center'>
                  {url ?
                    <img src={url} alt='logo' className={classes.logoImage} /> :
                    <Typography variant='h5' className={classes.logoText}>
                      {id}
                    </Typography>}
                </Grid>
              </Flip>
            </Card>
          </Grid>
        ))}
      </Grid>
    );
  }

  render() {
    return (
      <Fade in={true} timeout={ANIMATION_TIMEOUT}>
        <Grid item className={this.props.classes.container}>
          {this.Logos}
        </Grid>
      </Fade>
    );
  }
}

const mapStateToProps = state => {
  const {logos} = state.brandReducer;
  const logoUrls = logos.slice();
  return {logoUrls};
}

const mapDispatchToProps = _ => {
  return {};
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(
  withStyles(STYLE)(withTheme(BrandWall)))
);
