/****************************
 * Industry Count Component *
 ****************************/
import React from 'react';
import {connect} from 'react-redux';
import {withRouter} from 'react-router';

/** Styles. */
import {withStyles, withTheme} from '@material-ui/core/styles';
import {blueGrey} from '@material-ui/core/colors';
import STYLE from './IndustryCount.style';

/** Components. */
import {withWidth, Grid, Fade, Typography, Button, Tooltip} from '@material-ui/core';
import {PageLoader} from '../../../share/loader';
import {VerticalGap} from '../../../share/gap';
import {Slide} from 'react-reveal';

/** Util. */
import gradient from 'gradient-color';
import {redirect} from '../../../../utils/route';

/** Actions. */
import {searchBrandsByCategory} from '../../../../actions/brand';
import {updatePageLoad} from '../../../../dispatchers/loader';

/** Constants. */
import ROUTING_PATHS from '../../../../constants/routes';
import {SEARCH_CATEGORY} from '../../../../constants/properties';
import {ANIMATION_TIMEOUT} from '../../../../constants/animate';
const TOP_N = 15;

class IndustryCount extends React.Component {
  searchByCategory(name) {
    this.props.updatePageLoad(true);
    this.props.searchBrandsByCategory(SEARCH_CATEGORY.INDUSTRY, name)
      .then(() => {
        redirect(this.props, ROUTING_PATHS.SEARCH);
        this.props.updatePageLoad(false);
      });
  }
  get Industries() {
    const {industries, classes} = this.props
    if (!industries) {
      return <PageLoader />;
    }
    const backgroundColors = gradient(
      [blueGrey[900], blueGrey[500]], industries.length);
    return (
      <Grid container direction='row' spacing={1}>
        {industries.map(([industry, count], idx) => (
          <Grid item xs={12} sm={4} key={idx}>
            <Slide up cascade delay={idx * 50}>
              <Button variant='contained' className={classes.button}
                style={{backgroundColor: backgroundColors.shift()}}
                onClick={() => this.searchByCategory(industry)}>
                <Grid container direction='row' justify='space-between'>
                  <Tooltip title={industry} aria-label={industry}>
                    <Typography variant='overline' noWrap
                      style={{maxWidth: '80%'}}>
                      {industry}
                    </Typography>
                  </Tooltip>
                  <Typography variant='h6'>{count}</Typography>
                </Grid>
              </Button>
            </Slide>
          </Grid>
        ))}
      </Grid>
    );
  }

  render() {
    const {classes, width} = this.props;
    const isXs = width === 'xs';
    return (
      <Fade in={true} timeout={ANIMATION_TIMEOUT}>
        <Grid item className={classes.wrapper}>
          <Typography variant='h5' align={isXs ? 'center' : 'left'}>
            {TOP_N} Top Industries
          </Typography>
          <VerticalGap size='md' />
          {this.Industries}
        </Grid>
      </Fade>
    );
  }
}

const mapStateToProps = state => {
  const {industryCounts} = state.countReducer;
  const industries = industryCounts
    .sort((a, b) => b[1] - a[1])
    .slice(0, TOP_N);
  return {industries};
}

const mapDispatchToProps = dispatch => {
  return {
    searchBrandsByCategory: (category, keyword) =>
      dispatch(searchBrandsByCategory(category, keyword)),
    updatePageLoad: (isLoading) => dispatch(updatePageLoad(isLoading))
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(
  withWidth()(withStyles(STYLE)(withTheme(IndustryCount)))
));

