/******************************
 * Product Analysis Component *
 ******************************/
import React from 'react';
import {connect} from 'react-redux';
import {withRouter} from 'react-router';

/** Styles. */
import {withStyles, withTheme} from '@material-ui/core/styles';
import {lightBlue} from '@material-ui/core/colors';
import STYLE from './ProductAnalysis.style';

/** Components. */
import {withWidth, Grid, Fade, Button, Typography} from '@material-ui/core';
import {PageLoader} from '../../../share/loader';
import {VerticalGap} from '../../../share/gap';
import {Slide} from 'react-reveal';

/** Util. */
import gradient from 'gradient-color';
import {redirect} from '../../../../utils/route';

/** Actions. */
import {searchBrandsByCategory} from '../../../../actions/brand';
import {updatePageLoad} from '../../../../dispatchers/loader';

/** Constant. */
import ROUTING_PATHS from '../../../../constants/routes';
import {SEARCH_CATEGORY} from '../../../../constants/properties';
import {ANIMATION_TIMEOUT} from '../../../../constants/animate';
const TOP_N = 10;

class ProductAnalysis extends React.Component {
  searchByCategory(name) {
    this.props.updatePageLoad(true);
    this.props.searchBrandsByCategory(SEARCH_CATEGORY.PRODUCT, name)
      .then(() => {
        redirect(this.props, ROUTING_PATHS.SEARCH);
        this.props.updatePageLoad(false);
      });
  }

  get Products() {
    const {products, classes} = this.props
    if (!products) {
      return <PageLoader />;
    }
    const backgroundColors = gradient(
      [lightBlue[900], lightBlue[500]], products.length);
    return (
      <Grid container direction='column'>
        {products.map(([product, count], idx) => (
          <Slide right cascade key={idx} delay={idx * 50}>
            <Button variant='contained' className={classes.button}
              style={{backgroundColor: backgroundColors.shift()}}
              onClick={() => this.searchByCategory(product)}>
              <Grid container direction='row' justify='space-between'>
                <Typography variant='overline'>{product}</Typography>
                <Typography variant='h6'>{count}</Typography>
              </Grid>
            </Button>
          </Slide>
        ))}
      </Grid>
    )
  }

  render() {
    const {classes, width} = this.props;
    const isXs = width === 'xs';
    return (
      <Fade in={true} timeout={ANIMATION_TIMEOUT}>
        <Grid item className={classes.wrapper}>
          <Typography variant='h5' align={isXs ? 'center' : 'left'}>
            {TOP_N} Top sell products
          </Typography>
          <VerticalGap size='md' />
          {this.Products}
        </Grid>
      </Fade>
    );
  }
}

const mapStateToProps = state => {
  let {productCounts} = state.countReducer;
  const products = productCounts
    .sort((a, b) => b[1] - a[1])
    .slice(0, TOP_N);
  return {products};
}

const mapDispatchToProps = dispatch => {
  return {
    searchBrandsByCategory: (category, keyword) => dispatch(searchBrandsByCategory(category, keyword)),
    updatePageLoad: (isLoading) => dispatch(updatePageLoad(isLoading))
  };
}
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(
  withWidth()(withStyles(STYLE)(withTheme(ProductAnalysis)))
));
