/*********************
 * Insight Component *
 *********************/
import React from 'react';
import {connect} from 'react-redux';
import {withStyles, withTheme} from '@material-ui/core/styles';

/** Components. */
import {
  withWidth, Fade, Grid,
  List, ListItem, ListItemIcon, AppBar, Typography,
} from '@material-ui/core';
// import IndustryTable from './IndustryTable';
import BrandWall from './brand-wall/BrandWall';
import ProductAnalysis from './product-analysis/ProductAnalysis';
import CountryDistribution from './country-distribution/CountryDistribution';
import Overview from './overview/Overview';
import IndustryCount from './industry-count/IndustryCount';

/** Icons. */
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';

/** Constants. */
import {ANIMATION_TIMEOUT} from '../../../constants/animate';
import {INSIGHT_PAGE, PAGE_MAX_WIDTH} from '../../../constants/properties';
import STYLE from './Insight.style';
import {PageLoader} from '../../share/loader';

class Insight extends React.Component {
  state = {
    open: false,
    display: INSIGHT_PAGE.DISPLAY.OVERVIEW,
  }

  setDrawer(open) {
    this.setState({open, display: this.state.display});
  }

  setDisplay(display) {
    this.setState({display});
  }

  ListItems(items) {
    return (items.map(({text, icon, code}) => (
      <List key={text}>
        <ListItem button onClick={() => this.setDisplay(code)}>
          <ListItemIcon style={{minWidth: 'auto'}}>
            <FontAwesomeIcon icon={icon} size='lg' />
          </ListItemIcon>
        </ListItem>
      </List>
    )));
  }


  get SideNavBar() {
    const isXs = this.props.width === 'xs';
    return (
      <Grid container
        direction={isXs ? 'row' : 'column'}
        justify={isXs ? 'center' : 'center'}
        alignItems={isXs ? 'center' : 'center'}>
        {this.ListItems(INSIGHT_PAGE.TAGS)}
        {this.ListItems(INSIGHT_PAGE.STATISTICS)}
      </Grid>
    );
  }

  get Content() {
    switch (this.state.display) {
      case INSIGHT_PAGE.DISPLAY.OVERVIEW:
        return <Overview setDisplay={code => this.setDisplay(code)} />;
      case INSIGHT_PAGE.DISPLAY.BRAND_WALL:
        return <BrandWall />;
      case INSIGHT_PAGE.DISPLAY.INDUSTRY_COUNT:
        return <IndustryCount />;
      case INSIGHT_PAGE.DISPLAY.COUNTRY_DISTRIBUTION:
        return <CountryDistribution />;
      case INSIGHT_PAGE.DISPLAY.PRODUCT_ANALYSIS:
        return <ProductAnalysis />;
      default:
        return '';
    }
  }

  get InsightBody() {
    const {width, theme, isPageLoading} = this.props;
    if (isPageLoading) {
      return <PageLoader />;
    }
    const isXs = width === 'xs';
    return (
      <Grid container
        direction={isXs ? 'column' : 'row'}
        justify={isXs ? 'flex-start' : 'space-between'}
        alignItems={isXs ? 'center' : 'flex-start'}
        style={{maxWidth: PAGE_MAX_WIDTH}}>
        <Grid item xs={12} sm={1} style={{marginTop: theme.spacing(2)}}>
          {this.SideNavBar}
        </Grid>
        <Grid item xs={12} sm={11} style={{width: '100%'}}>
          {this.Content}
        </Grid>
      </Grid>
    );
  }

  get InsightHeader() {
    return (
      <AppBar position='static' elevation={0}>
        <Grid container direction='row' justify='center'
          style={{padding: this.props.theme.spacing(3)}}>
          <Typography variant='h6'>
            {INSIGHT_PAGE.STATS_TITLE[this.state.display]}
          </Typography>
        </Grid>
      </AppBar>
    );
  }

  render() {
    return (
      <Grid container direction='column' alignItems='center'>
        {this.InsightHeader}
        <Fade in={true} timeout={ANIMATION_TIMEOUT}>
          {this.InsightBody}
        </Fade>
      </Grid>
    );
  }
}

const mapStateToProps = state => {
  const {brands} = state.brandReducer;
  const {isPageLoading} = state.loaderReducer;
  return {brands, isPageLoading};
}

const mapDispatchToProps = _ => {
  return {};
}

export default connect(mapStateToProps, mapDispatchToProps)(
  withWidth()(withStyles(STYLE)(withTheme(Insight)))
);
