/*****************
 * Count Reducer *
 *****************/

/** Constant. */
import {Count} from '../../constants/redux';

const defaultState = Object.freeze({
  countryCounts: [],
  productCounts: [],
  industryCounts: [],
  overviewCount: {},
});

export default (state = defaultState, action) => {
  switch (action.type) {
    case Count.UPDATE_PRODUCT_COUNT:
      const productCounts = action.data.slice();
      return Object.assign({}, state, {productCounts});
    case Count.UPDATE_INDUSTRY_COUNT:
      const industryCounts = action.data.slice();
      return Object.assign({}, state, {industryCounts});
    case Count.UPDATE_COUNTRY_COUNT:
      const countryCounts = action.data.slice();
      return Object.assign({}, state, {countryCounts});
    case Count.UPDATE_COUNT_OVERVIEW:
      const overviewCount = Object.freeze(action.data);
      return Object.assign({}, state, {overviewCount});
    default:
      return state;
  }
}
