/**************
 * Count Util *
 **************/

/** HTTP client. */
import {get} from './http';

/** URL endpoints. */
import {COUNT_CATEGORY_URL, COUNT_OVERVIEW_URL} from '../constants/api';

export const getCategoryCount = categoryName => {
  const url = `${COUNT_CATEGORY_URL}/${categoryName}`;
  return get(url)
    .then(data => data)
    .catch(error => error);
}

export const getCountOverview = () => {
  const url = COUNT_OVERVIEW_URL;
  return get(url)
    .then(data => data)
    .catch(error => error);
}
