/*******************
 * Grid Components *
 *******************/
import React from 'react';
import {withStyles} from '@material-ui/core/styles';
import {white} from '@material-ui/core/colors/common'
import Theme from '../../constants/theme';

/** Components. */
import {Paper, Grid, Typography} from '@material-ui/core';

export const SectionHighlight = ({isFocus}) => {
  return (
    <div style={{
      position: 'absolute', top: 0, left: 0,
      width: 3, height: '100%',
      borderRadius: '4px 0 0 4px',
      backgroundColor: isFocus ?
        Theme.palette.primary[500] : white,
      transition: 'all 350ms'
    }}>
    </div>
  );
};

export const Section = withStyles(theme => {
  return {
    root: {
      width: '100%', padding: '48px 16px 32px', position: 'relative',
      [theme.breakpoints.down('xs')]: {
        margin: '0 8px'
      }
    },
  }
})(Paper);

export const Ribbon = ({text}) => {
  const themeColor = Theme.palette.primary.main;
  const ribbonHeight = 40;
  return (
    <Grid container direction="row" style={{
      position: 'absolute', top: 0, left: 0,
    }}>
      <Typography variant="overline" style={{
        content: text, color: 'white', background: themeColor,
        padding: '5px 8px 3px 24px', borderRadius: '4px 0 0 0'
      }}>{text}
      </Typography>
      <svg height={ribbonHeight} width={ribbonHeight}>
        <polygon points={`0,0 ${ribbonHeight},0 0,${ribbonHeight}`}
          style={{fill: themeColor}}></polygon>
      </svg>
    </Grid>
  );
};

export const SectionTitle = ({text}) => (
  <Typography variant="h4">{text}</Typography>
);

export const SectionSubTitle = ({text}) => (
  <Typography variant="subtitle1">{text}</Typography>
);

