import React from 'react';
import clsx from 'clsx';
import Select from 'react-select';
import {emphasize, makeStyles, useTheme} from '@material-ui/core/styles';
import {
  Typography, TextField, Paper, Chip, MenuItem, FormControl, FormHelperText,
} from '@material-ui/core';
import CancelIcon from '@material-ui/icons/Cancel';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    height: 250,
    minWidth: 290,
  },
  input: {
    display: 'flex',
    padding: 0,
    height: 'auto',
  },
  valueContainer: {
    display: 'flex',
    flexWrap: 'wrap',
    flex: 1,
    alignItems: 'center',
    overflow: 'hidden',
  },
  chip: {
    margin: theme.spacing(0.5, 0.25),
  },
  chipFocused: {
    backgroundColor: emphasize(
      theme.palette.type === 'light' ?
        theme.palette.grey[300] : theme.palette.grey[700],
      0.08,
    ),
  },
  noOptionsMessage: {
    padding: theme.spacing(1, 2),
  },
  singleValue: {
    fontSize: 16,
  },
  placeholder: {
    position: 'absolute',
    left: 2,
    bottom: 6,
    fontSize: 16,
    color: 'rgba(0, 0, 0, 0.4)'
  },
}));

function NoOptionsMessage(props) {
  return (
    <Typography
      color="textSecondary"
      className={props.selectProps.classes.noOptionsMessage}
      {...props.innerProps}>
      {props.children}
    </Typography>
  );
}

function inputComponent({inputRef, ...props}) {
  return <div ref={inputRef} {...props} />;
}

function Control(props) {
  const {children, innerProps, innerRef, selectProps: {classes, TextFieldProps},
  } = props;

  return (
    <TextField
      fullWidth
      InputProps={{
        inputComponent,
        inputProps: {
          className: classes.input,
          ref: innerRef,
          children,
          ...innerProps,
        },
      }}
      {...TextFieldProps}
    />
  );
}

function Option(props) {
  return (
    <MenuItem
      ref={props.innerRef}
      selected={props.isFocused}
      component="div"
      style={{
        fontWeight: props.isSelected ? 500 : 400,
      }}
      {...props.innerProps}
    >
      {props.children}
    </MenuItem>
  );
}

function Placeholder(props) {
  const {selectProps, innerProps = {}, children} = props;
  return (
    <Typography color="textSecondary" {...innerProps}
      className={selectProps.classes.placeholder} >
      {children}
    </Typography>
  );
}

function SingleValue(props) {
  return (
    <Typography className={props.selectProps.classes.singleValue} {...props.innerProps}>
      {props.children}
    </Typography>
  );
}

function ValueContainer(props) {
  return (
    <div className={props.selectProps.classes.valueContainer}>
      {props.children}
    </div>
  );
}

function MultiValue(props) {
  return (
    <Chip
      tabIndex={-1}
      label={props.children}
      className={clsx(props.selectProps.classes.chip, {
        [props.selectProps.classes.chipFocused]: props.isFocused,
      })}
      onDelete={props.removeProps.onClick}
      deleteIcon={<CancelIcon {...props.removeProps} />}
    />
  );
}

function Menu(props) {
  return (
    <Paper square {...props.innerProps} elevation={2}>
      {props.children}
    </Paper>
  );
}

const components = {
  Control, Menu, Placeholder,
  MultiValue, SingleValue, ValueContainer,
  NoOptionsMessage, Option,
};

export const AutoCompleteSelect = (props) => {
  const [classes, theme] = [useStyles(), useTheme()];
  const selectStyles = {
    input: base => ({
      ...base,
      color: theme.palette.text.primary,
    }),
  };
  let {
    hasError, label, name, value, options, isMulti,
    helperText, placeholder,
    onChange, onBlur
  } = props;
  options = options.map(suggestion => ({
    value: suggestion, label: suggestion,
  }));
  if (isMulti) {
    value = value.map(val => ({value: val, label: val}));
  } else {
    if (value) {
      value = {value: value, label: value};
    }
  }

  return (
    <FormControl required error={hasError} style={{width: '100%'}} >
      <Select
        name={name} options={options} components={components}
        value={value} isMulti={isMulti}
        onChange={onChange} onBlur={onBlur}
        classes={classes} styles={selectStyles}
        placeholder={placeholder}
        TextFieldProps={{
          label: `${label}*`,
          InputLabelProps: {shrink: true}
        }}
      />
      <FormHelperText>{helperText}</FormHelperText>
    </FormControl>
  );
}
