/*********************
 * Gallery Component *
 *********************/
import React from 'react';
import {connect} from 'react-redux';
import {withTheme, withStyles} from '@material-ui/core/styles';

/** Components. */
import {withWidth, Grid, Fade, Button} from '@material-ui/core';
import {VerticalGap} from '../../share/gap';
import {BrandCards} from '../../share/card';

/** Constants. */
import {ANIMATION_TIMEOUT} from '../../../constants/animate';
import {PAGE_MAX_WIDTH} from '../../../constants/properties';
import STYLE from './Gallery.style';

/** Actions. */
import {fetchBrands} from '../../../actions/brand';
import {updatePageLoad} from '../../../dispatchers/loader';

/** Icons. */
import {
  NavigateBefore, NavigateNext, FirstPage, LastPage
} from '@material-ui/icons';

class Gallery extends React.Component {
  getPage(nextPage) {
    const {updatePageLoad, currentPage, firstPage, lastPage} = this.props;
    if (nextPage < firstPage || nextPage > lastPage ||
      nextPage === currentPage) {
      return;
    }
    updatePageLoad(true)
    this.props.fetchBrands(nextPage).then(() => updatePageLoad(false));
  }

  get pageNumbers() {
    const {width, currentPage, firstPage, lastPage} = this.props;
    const isXs = width === 'xs';
    const pageLimit = isXs ? 3 : 5;
    let pageNumbers = new Array(lastPage).fill().map((_, i) => i + 1);
    if (pageNumbers.length <= pageLimit) {
      return pageNumbers;
    }
    const halfUnit = Math.floor(pageLimit / 2);
    if (currentPage <= (firstPage + halfUnit)) {
      return pageNumbers.slice(0, pageLimit);
    } else if (currentPage >= (lastPage - halfUnit)) {
      return pageNumbers.slice(-pageLimit);
    } else {
      const start = currentPage - halfUnit - 1; // convert to index
      return pageNumbers.slice(start, start + pageLimit);
    }
  }

  get PaginateBar() {
    const {
      classes, theme, width, hasPrev, hasNext,
      currentPage, firstPage, lastPage,
    } = this.props;
    const isXs = width === 'xs';

    const pageButtons = this.pageNumbers.map(pageNumber => ({
      disabled: false,
      onClick: () => this.getPage(pageNumber),
      display: pageNumber,
      isNumberBtn: true,
    }));

    const buttons = [
      {
        disabled: !hasPrev,
        onClick: () => this.getPage(firstPage),
        display: <FirstPage />,
        isNumberBtn: false,
      }, {
        disabled: !hasPrev,
        onClick: () => this.getPage(currentPage - 1),
        display: <NavigateBefore />,
        isNumberBtn: false,
      },
      ...pageButtons,
      {
        disabled: !hasNext,
        onClick: () => this.getPage(currentPage + 1),
        display: <NavigateNext />,
        isNumberBtn: false,
      }, {
        disabled: !hasNext,
        onClick: () => this.getPage(lastPage),
        display: <LastPage />,
        isNumberBtn: false,
      },
    ];
    return (
      <Grid item sm={12} className={classes.paginateBar}>
        <Grid container direction='row' justify='center'>
          {buttons.map(({disabled, onClick, display, isNumberBtn},
            idx) => (
              <Button key={idx} disabled={disabled} onClick={onClick}
                size='small' color='primary'
                variant={isNumberBtn && display === currentPage ?
                  'contained' : 'outlined'}
                style={{
                  minWidth: 'inherit', margin: isXs ?
                    theme.spacing(0, 0.2) : theme.spacing(0, 0.5),
                  boxShadow: theme.shadows[0],
                  padding: isNumberBtn ? theme.spacing(0, 1.5) : 'inherit'
                }}>
                {display}
              </Button>
            ))}
        </Grid>
      </Grid>
    );
  }

  render() {
    const {width, theme} = this.props;
    const isXs = width === 'xs';
    return (
      <Fade in={true} timeout={ANIMATION_TIMEOUT}>
        <Grid style={{margin: `0 auto 50px`}}>
          {!isXs && <VerticalGap size='md' />}
          <Grid container style={{
            padding: theme.spacing(3),
            maxWidth: PAGE_MAX_WIDTH
          }}>
            {<BrandCards brands={this.props.brands} fit='merge' />}
            {this.PaginateBar}
          </Grid>
        </Grid>
      </Fade>
    );
  }
}

const mapStateToProps = state => {
  const {brands, currentPage, lastPage} = state.brandReducer;
  const firstPage = 1;
  const hasNext = currentPage < lastPage;
  const hasPrev = currentPage > firstPage;
  return {
    brands: brands.slice(), firstPage, currentPage, lastPage,
    hasNext, hasPrev
  };
}

const mapDispatchToProps = dispatch => {
  return {
    fetchBrands: (page) => dispatch(fetchBrands(page)),
    updatePageLoad: (isLoading) => dispatch(updatePageLoad(isLoading))
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(
  withWidth()(withStyles(STYLE)(withTheme(Gallery)))
);

