export const parseBrandDetail = brand => {
  return {
    id: brand['id'],
    name: brand['name'],
    industries: brand['industries'],
    introduction: brand['introduction'],
    keyPeople: brand['key_people'],
    foundedYear: brand['founded_year'],
    headquarterCountries: brand['headquarter_countries'],
    products: brand['products'],
    website: brand['website'],
    mission: brand['mission'],
    logoUrl: brand['logo_url'],
    joinedAt: brand['joined_at'],
    articleCount: brand['article_count']
  }
}


export const parseArticleDetail = article => {
  return {
    id: article['id'],
    title: article['title'],
    summary: article['summary'],
    postedAt: article['posted_at'],
  }
}
