/******************
 * Dialog Reducer *
 ******************/

 /** Constant. */
import {Dialog} from '../../constants/redux';

const defaultState = Object.freeze({
  isBrandSubmissionDialogOpen: false,
})

export default (state = defaultState, action) => {
  switch (action.type) {
    case Dialog.OPEN_BRAND_SUBMISSION_DIALOG:
      return Object.assign({}, state, {
        isBrandSubmissionDialogOpen: true
      });
    case Dialog.CLOSE_BRAND_SUBMISSION_DIALOG:
      return Object.assign({}, state, {
        isBrandSubmissionDialogOpen: false
      });
    default:
      return state;
  }
}
