import React from 'react';
import {withStyles} from '@material-ui/core/styles';
import {grey} from '@material-ui/core/colors'

/** Components. */
import {Paper, Typography, Avatar} from '@material-ui/core';

export const DetailCard = withStyles(theme => ({
  root: {padding: theme.spacing(2)}
}))(Paper);

export const DetailCardTitle = ({text}) => {
  return (
    <Typography variant="caption" style={{color: grey[600], fontWeight: 300}}>
      {text}
    </Typography>
  );
};

export const DetailLogo = withStyles(theme => ({
  root: {
    padding: theme.spacing(2),
    width: '100%', height: '100%',
    backgroundColor: theme.palette.common.white,
    // [theme.breakpoints.down('xs')]: {
    //   width: theme.spacing(5), height: theme.spacing(5),
    // }
  },
  img: {height: 'auto'}
}))(Avatar);

