import React from 'react';
import {connect} from 'react-redux';
import {withRouter} from 'react-router';
import {withTheme, withStyles} from '@material-ui/core/styles';

/** Components. */
import {
  withWidth,
  AppBar, Toolbar, Typography, IconButton,
  Grid, Tooltip, Button, InputBase, Fab
} from '@material-ui/core';
import {faPhone} from '@fortawesome/free-solid-svg-icons';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';

/** Icon. */
import {Search, Close} from '@material-ui/icons';

/** Constant. */
import ROUTING_PATHS from '../../constants/routes';
import {NAV_BAR} from '../../constants/properties';
import STYLE from './NavigationBar.style';

/** Util. */
import {redirect} from '../../utils/route';

/** Actions. */
import {searchBrandsByKeyword} from '../../actions/brand';
import {updatePageLoad} from '../../dispatchers/loader';

class NavigationBar extends React.Component {
  state = {
    searchKeyword: '',
    onSearchPage: false,
  }

  updateSearchKeyword(searchKeyword) {
    this.setState({searchKeyword});
  }

  static getDerivedStateFromProps(props, state) {
    if (props.location.search) {
      const routeKeyword = new URLSearchParams(props.location.search)
        .get('keyword');

      // Skip if the props keyword equals to the route keyword
      if (props.searchKeyword === routeKeyword) {
        // for the state to pick up keyword from route on init loading
        if (!state.onSearchPage) {
          state.searchKeyword = routeKeyword;
          state.onSearchPage = true;
        }
        return null;
      }

      if (props.searchKeyword !== routeKeyword) {
        state.searchKeyword = routeKeyword;
        props.updatePageLoad(true);
        props.searchBrandsByKeyword(routeKeyword).then(() => {
          props.updatePageLoad(false);
        });
      }
    }
    return null;
  }

  submitSearch(key) {
    if (key === 'Enter') {
      const {searchKeyword} = this.state;
      redirect(this.props, ROUTING_PATHS.SEARCH, searchKeyword);
    }
  }

  clearSearch() {
    this.setState({searchKeyword: ''});

  }

  /** Media Icon information. */
  get mediaIcons() {
    return NAV_BAR.LINK_ICONS.map((icon, idx) => (
      <IconButton key={idx} color='inherit' href={icon.link} target='_blank'>
        <FontAwesomeIcon icon={icon.icon} size='1x' />
      </IconButton>
    ));
  }

  /** Phone information. */
  get phoneInfo() {
    return (
      <Tooltip title={NAV_BAR.CONTACT.tip} placement='bottom'>
        <IconButton color='inherit'>
          <FontAwesomeIcon icon={faPhone} size='xs' />
        </IconButton>
      </Tooltip>
    );
  }

  get SearchInput() {
    const {classes, width} = this.props;
    const isXs = width === 'xs';
    return (
      <Grid container className={classes.search}
        direction='row' alignItems='center' justify='space-between'>
        {!isXs &&
          <Grid item>
            <Fab size='small' className={classes.searchIcon}>
              <Search />
            </Fab>
          </Grid>}
        <Grid item className={classes.inputWrapper}>
          <InputBase placeholder='Search...'
            value={this.state.searchKeyword}
            onChange={event => this.updateSearchKeyword(event.target.value)}
            onKeyDown={event => this.submitSearch(event.key)}
            classes={{root: classes.inputRoot, input: classes.inputInput}} />
        </Grid>
        {!isXs && <Grid item>
          <Fab size='small' className={classes.clearButton}
            onClick={() => this.clearSearch()}>
            <Close />
          </Fab>
        </Grid>}
      </Grid>
    );
  }

  render() {
    const {classes, width} = this.props;
    const isXs = width === 'xs';
    return (
      <AppBar position='static' elevation={0}>
        <Toolbar className={classes.toolbar}>
          <Grid container direction='row'
            justify='space-between' alignItems='center'>
            <Grid item sm={2} xs={3}>
              <Button color='inherit' onClick={() =>
                redirect(this.props, ROUTING_PATHS.GALLERY)}>
                <Typography variant='h6'>
                  {NAV_BAR.LOGO.text}
                </Typography>
              </Button>
            </Grid>
            <Grid item sm={10} xs={9}>
              <Grid container direction='row' alignItems='center'>
                {this.SearchInput}
                {!isXs && this.phoneInfo}
                {this.mediaIcons}
              </Grid>
            </Grid>
          </Grid>
        </Toolbar>
      </AppBar>
    );
  }
}

const mapStateToProps = state => {
  const {searchKeyword} = state.brandReducer;
  return {searchKeyword};
}

const mapDispatchToProps = dispatch => {
  return {
    searchBrandsByKeyword: (keyword) => dispatch(searchBrandsByKeyword(keyword)),
    updatePageLoad: (isLoading) => dispatch(updatePageLoad(isLoading))
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(
  withWidth()(withStyles(STYLE)(withTheme(NavigationBar))))
);
